<script>
import { mdbBtn, mdbCard, mdbCardBody, mdbCardText, mdbIcon, mdbBreadcrumb, mdbBreadcrumbItem } from "mdbvue";
import Imagenes from '../components/imagenes.vue';

export default {
  components: {
    mdbBtn,
    mdbCard,
    mdbCardBody,
    mdbCardText,
    mdbIcon,
    mdbBreadcrumb, mdbBreadcrumbItem,
    Imagenes
  },
  data() {
    return {
      fourRandom: [],
      id: this.$route.params.id,
      product: {},
    };
  },
  props: {
    items: Array,
  },
  mounted() {
    this.getProduct();
    document.title = `Sistemas de Seguridad y Protección - ${this.product.Nombre}`;
  },
  updated() {
    document.title = `Sistemas de Seguridad y Protección - ${this.product.Nombre}`;
  },
  watch: {
    $route(to) {
      this.id = to.params.id;
      this.getProduct();
    },
  },
  methods: {
    getProduct() {
      this.items.forEach((item) => {
        if (item.ID == this.id) {
          this.product = item;
        }
      });
    },
  },
};
</script>

